.hero {
  position: relative; }
  .hero .hero__image {
    width: 100%;
    height: 100vh;
    max-height: 650px;
    object-fit: cover; }

.hero--full .hero__figure {
  width: 100vw;
  position: relative;
  margin-left: -50vw;
  left: 50%; }
  .hero--full .hero__figure::after {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: black;
    background: linear-gradient(0deg, black 0%, rgba(0, 0, 0, 0) 100%); }

.hero--full .hero__content {
  position: absolute;
  bottom: 4rem;
  z-index: 10;
  color: var(--white); }

.hero--split .hero__container {
  display: flex;
  width: 100vw;
  position: relative;
  margin-left: -50vw;
  left: 50%; }

.hero--split .hero__content {
  padding: 4rem;
  width: 50%; }

.hero--split .hero__figure {
  width: 50%; }

.hero--left .hero__container {
  flex-direction: row-reverse; }
